import React, { useState } from "react";
import styled from "styled-components";

const Wrapper = styled.section`
  >ul{
  background-color: #f9db6a;
  display: flex;
  text-align: center;
  > li {
    width: 50%;
    height: 64px;
    display: flex;
    justify-content: center;
    font-size: 24px;
    align-items: center;
    position: relative;
    &.selected::after {
      content: "";
      position: absolute;
      left: 0px;
      bottom: 0px;
      width: 100%;
      height: 4px;
      background-color: #555555;
    }
  }
  }
`
type Props = {
    value: '-' | '+';
    onChange: (value: '-' | '+') => void
}

const CategorySection: React.FC<Props> = (props) => {
    const categoryMap = { '-': '支出', '+': '收入' };
    const [categoryList] = useState<('+' | '-')[]>(['-', '+']);
    const categroy = props.value
    return (
        <Wrapper>
            <ul>
                {categoryList.map(c =>
                    <li key={c}
                        className={categroy === c ? 'selected' : ''}
                        onClick={() => { props.onChange(c); }}
                    >{categoryMap[c]}
                    </li>
                )}
            </ul>
        </Wrapper>
    )
}
export { CategorySection }