
import { useTags } from "components/hooks/useTags";
import React from "react";
import styled from "styled-components";

const Wrapper = styled.div`
flex-grow: 2;
margin: auto;
margin-top: 20px;
width: 90%;
>div{
  display: flex;
  flex-wrap: wrap;
  > div {
    display: flex;
    /* border: 1px solid red; */
    width: 25%;
    height: 50px;
    margin-bottom: 20px;
    justify-content: center;
    > span {
      display: inline-block;
      height: 50px;
      width: 70px;
      background: #f3f3f3;
      border-radius: 10px;
      text-align: center;
      line-height: 50px;
    }
    &.selected > span {
      background: #f9db6a;
    }
    }
}
`
type Props = {
    value: number[]
    onChange: (selected: number[]) => void
}
const TagsSection: React.FunctionComponent<Props> = (props) => {
    const { tags, addTag } = useTags()
    const selectedTagIds = props.value;
    const onToggleTag = (tagId: number) => {
        const index = selectedTagIds.indexOf(tagId)
        if (index >= 0) {
            props.onChange(selectedTagIds.filter(t => t !== tagId))
        } else {
            props.onChange([...selectedTagIds, tagId])
        }
    }


    return (
        <Wrapper>
            <div>
                {tags.map(tag =>
                    <div key={tag.id} onClick={() => { onToggleTag(tag.id) }} className={selectedTagIds.indexOf(tag.id) >= 0 ? 'selected' : ''}><span>{tag.name}</span></div>
                )}
                <div onClick={addTag}><span>+</span></div>
            </div>
        </Wrapper>
    )
}
export { TagsSection }