import { useEffect, useRef } from "react"

export const useUpdate = (fn: () => void, dependency: any[]) => {
    const count = useRef(0)
    useEffect(() => {
        count.current += 1
    })
    // 存数据：当tags发生变化时把tags的内容存到localstorage中
    useEffect(() => {
        if (count.current > 1) {
            fn();
        }
    }, [fn, dependency])
}
