import Layout from '../components/Layout';
import React from 'react';
import Icon from 'components/Icon';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import { useTags } from 'components/hooks/useTags';

const TagList = styled.div`
     flex-grow: 2;
  // border: 1px solid rgb(17, 10, 10);
  position: relative;
  > div{
    font-size: 16px;
    max-height: 600px;
    overflow: auto;
    >a{
      display: flex;
      padding: 10px 15px 10px 5px;
      border-bottom: 1px solid #f3f3f3;
      justify-content: space-between;
      align-items: center;
      >span{
        height: 44px;
        min-width: 44px;
        background: #f9db6a;
        border-radius: 10px;
        text-align: center;
        line-height: 44px;
        padding: 0px 5px;
      }
      >icon{
        width: 20px;
        height: 20px;
      }
    }
  }
  > .createTag-wrapper {
    // position: absolute;
    // border: 1px solid blue;
    text-align: center;
    padding: 24px;
    > .createTag {
      // border: 1px solid red;
      font-size: 16px;
      background: $color-shadow;
      border: none;
      border: 0.5px solid #404040;
      border-radius: 5px;
      padding: 5px 16px;
    }
  }
`
// label标签页面的tags和新增tags
function Tags() {
  const { tags, addTag } = useTags()
  return (
    <Layout>
      <TagList>
        <div>
          {tags.map(tag =>
            <Link to={'/tags/' + tag.id} key={tag.id}>
              {/* router-link */}
              <span >{tag.name}</span>
              <Icon name="right" />
            </Link>)}

        </div>
        <div className="createTag-wrapper">
          <button className="createTag" onClick={addTag}>
            <Icon name="add" />添加类别
          </button>
        </div>
      </TagList>

    </Layout>
  );
}

export default Tags;