import { useTags } from "../components/hooks/useTags"
import Icon from "components/Icon"
import Layout from "components/Layout"
import React from "react"
import { useParams, useHistory } from "react-router-dom"
import styled from "styled-components"



type Paramas = {
  id: string
}
const Wrapper = styled.div`

  >.newBar{
  text-align: center;
  font-size: 16px;
  padding: 12px 16px;
  // border: 1px solid red;
  background: #f9db6a;
  display: flex;
  align-items: center;
  justify-content: space-between;

  > .icon {
    color: #1c191c;
    width: 24px;
    height: 24px;
  }
  > .rightIcon {
    width: 24px;
    height: 24px;
  }
}
.notes {
  font-size: 14px;
  background: white;
  border: 1px solid #f3f3f3;
  padding-left: 16px;
  display: flex;
  align-items: center;
  .name {
    padding-right: 16px;
  }
  input {
    height: 54px;
    flex-grow: 1;
    background: transparent;
    border: none;
    padding-right: 16px;
  }
}
.createTag-wrapper {
  text-align: center;
  padding-top: 20px;

  margin: 0 auto;
  > .createTag {
    margin-right: 60px;
    font-size: 16px;
    background: $color-shadow;
    border: none;
    border: 0.5px solid #404040;
    border-radius: 5px;
    padding: 5px 16px;
  }
  > .saveTag {
    font-size: 16px;
    background: $color-shadow;
    border: none;
    border: 0.5px solid #404040;
    border-radius: 5px;
    padding: 5px 16px;
  }
}

`

// label下的editlabel页面，对页面进行编辑和删除
const Tag = () => {
  const { findTag, updateTag, } = useTags();
  let { id: idString } = useParams<Paramas>();
  const tag = findTag(parseInt(idString));
  const history = useHistory()
  const onClickBack = () => {
    history.goBack()
  }
  if (tag) {
    return (
      <Layout>
        <Wrapper>
          <div className="newBar">
            <Icon name="left" onClick={onClickBack} />
            <span className="title">编辑类别</span>
            <span className="rightIcon" ></span >
          </div>

          {tag ? <div>
            < label className="notes" >
              <span className="name">标签名：</span>
              <input type="text" placeholder="标签名"
                value={tag.name}
                onChange={(e) => {
                  updateTag(tag.id, { name: e.target.value });
                }} />
            </label >

            <div className="createTag-wrapper">
              {/* <button className="createTag" onClick={() => deleteTag(tag.id)}>删除</button> */}
              <button className="saveTag" onClick={onClickBack}>保存</button>
            </div>
          </div> : <div>tag不存在</div>}

        </Wrapper>
      </Layout >

    )
  } else {
    return (
      <div>
        tag不存在
      </div>
    )
  }
}
export { Tag }